module.exports = "const glyphInstance = (\n  <div>\n    <ButtonToolbar>\n      <ButtonGroup>\n        <Button>\n          <Glyphicon glyph=\"align-left\" />\n        </Button>\n        <Button>\n          <Glyphicon glyph=\"align-center\" />\n        </Button>\n        <Button>\n          <Glyphicon glyph=\"align-right\" />\n        </Button>\n        <Button>\n          <Glyphicon glyph=\"align-justify\" />\n        </Button>\n      </ButtonGroup>\n    </ButtonToolbar>\n    <ButtonToolbar>\n      <ButtonGroup>\n        <Button bsSize=\"large\">\n          <Glyphicon glyph=\"star\" /> Star\n        </Button>\n        <Button>\n          <Glyphicon glyph=\"star\" /> Star\n        </Button>\n        <Button bsSize=\"small\">\n          <Glyphicon glyph=\"star\" /> Star\n        </Button>\n        <Button bsSize=\"xsmall\">\n          <Glyphicon glyph=\"star\" /> Star\n        </Button>\n      </ButtonGroup>\n    </ButtonToolbar>\n  </div>\n);\n\nrender(glyphInstance);\n"


